<template>
	<div>
		<van-nav-bar class="hader" title="今日主题" left-text="返回" left-arrow @click-left="back()">
			<a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS != true && isWeixin != true"><van-button type="primary" size="small">安装APP</van-button></a>
			<a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS == true && isWeixin != true">
				<van-button type="primary" size="small">安装APP</van-button>
			</a>
			<a href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU0NTg5ODQzNA==&scene=124#wechat_redirect" slot="right" v-if="isWeixin == true">
				<van-button type="primary" size="small">关注</van-button>
			</a>
		</van-nav-bar>

		<LoadMore :onLoadMore="onLoadMore" :enableLoadMore="enableLoadMore">
			<div class="cont mt10 clearfix">
				<div class="food-list1">
					<ul>
						<li v-for="(item, index) in themeList" :key="index" @click="bookDetail(item.topicid)">
							<div class="foodlist-img" :style="{ backgroundImage: 'url(' + osshost + item.topicimg + listpic + ')' }"></div>
							<div class="food-list1-tit text-cut">{{ item.title }}</div>
							<div class="user">
								<img :src="osshost + item.faceimg + userface" />
								<span>{{ item.nickname }}</span>
								<i class="icon-like2 van-icon van-icon-like">{{ item.collections }}</i>
							</div>
						</li>
					</ul>
					<!-- </mu-load-more> -->
				</div>
			</div>
		</LoadMore>
		<Totop></Totop>
	</div>
</template>

<script>
	import qs from 'qs';
	import axios from 'axios';
import LoadMore from '../components/LoadMore.vue';
import Totop from '../components/totop.vue';
export default {
	name: 'cookbookinfo',
	components: {
		LoadMore,
		Totop
	},
	data() {
		return {
			num: 13,
			enableLoadMore: true,
			page: 1,
			themeList: [],
			osshost: 'https://guanfan.oss-cn-hangzhou.aliyuncs.com/',
			listpic: '?x-oss-process=style/list-img',
			userface: '?x-oss-process=style/user-img',
			detailpic: '?x-oss-process=style/detail-img',
			isWeixin: '',
			isiOS: ''
		};
	},
	created() {
		this.getTheme();
		this.getwx();
		this.appSource();
	},
	methods: {
		getwx() {
			var ua = window.navigator.userAgent.toLowerCase();
			if (ua.match(/MicroMessenger/i) == 'micromessenger') {
				this.isWeixin = true;
			} else {
				this.isWeixin = false;
			}
		},
		appSource() {
			const u = navigator.userAgent,
				app = navigator.appVersion;
			if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
				this.isiOS = true;
			} else {
				this.isiOS = false;
			}
		},
		onLoadMore(done) {
			setTimeout(() => {
				if (!this.enableLoadMore) {
					return;
				}
				this.page = this.page + 1;
				this.getThemeList();
				done();
			}, 500);
		},
		getTheme() {
			// getThemeList(this.page, 1)
			// 	.then(res => {
			// 		if (res.data.result.length < 10) {
			// 			this.enableLoadMore = false;
			// 		}
			// 		this.themeList = this.themeList.concat(res.data.result);
			// 	})
			// 	.catch(error => {
			// 		console.log(error);
			// 	});
				let data = qs.stringify({
					topictype:1,
					page:this.page
				});
				axios({
					method: 'post',
					url: '/subject/topiclist',
					headers: {
						'Content-Type': 'application/x-www-form-urlencoded'
					},
					data
				}).then(response => {
					if (response.data.result.length < 10) {
						this.enableLoadMore = false;
					}
					this.themeList = this.themeList.concat(response.data.result);
				});
		},
		bookDetail(id) {
			this.$router.push({ path: '/themeinfo', query: { id: id } });
		},
		back() {
			this.$router.go(-1); //返回上一层
		}
	}
};
</script>

<style></style>
